.indexpage {
  background: #eee;
  padding: 40px 0;
}
.indexpage .jackma {
  width: 343px;
  height: 300px;
  background: #fff;
  border-radius: 20px;
  margin: 0 auto;
  padding-top: 20px;
  margin-top: 30px;
}
.indexpage .jackma .header_img {
  width: 150px;
  height: 150px;
  background: url("/images/jack-ma.jpg") no-repeat;
  background-size: cover;
  margin: 0 auto;
  border-radius: 50%;
}
.indexpage .jackma h3 {
  font-size: 24px;
  color: #ffa040;
  text-align: center;
  margin-top: 30px;
}
.indexpage .jackma .balance {
  margin-top: 20px;
}
.indexpage .jackma .balance .balance_baby {
  width: 160px;
  height: 40px;
  background: #ff5722;
  color: #ffffff;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  margin: 0 auto;
  display: block;
}
.indexpage .jackma .balance .balance_baby_on {
  width: 160px;
  height: 40px;
  background: #999999;
  color: #ffffff;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  margin: 0 auto;
  display: block;
}
.indexpage .jackma .balance span {
  text-align: center;
  font-size: 14px;
  color: #666;
  display: block;
  margin: 0 auto;
  margin-top: 15px;
}
.indexpage .mall {
  width: 350px;
  height: 50px;
  color: #ffffff;
  background: #ff5722;
  margin: 0 auto;
  margin-top: 20px;
  border-radius: 5px;
}
.indexpage .mall ul {
  display: flex;
  justify-content: space-around;
  line-height: 50px;
}
.indexpage .mall ul li {
  width: 80px;
  height: 44px;
  font-size: 18px;
  text-align: center;
}
.indexpage .mall ul li a {
  color: #ffffff;
}
.indexpage .mall ul .li_on {
  border-bottom: 1px solid #ffa040;
}
.indexpage .mall ul .li_on a {
  color: #ffffff;
}
.indexpage .auto_list {
  width: 350px;
  height: 300px;
  background: #ffffff;
  margin: 20px auto;
  border-radius: 5px;
}
.indexpage .auto_list img {
  width: 100%;
  height: 192px;
  border-radius: 5px 5px 0 0;
}
.indexpage .auto_list h3 {
  font-size: 24px;
  color: #000000;
  margin: 10px 0 0 15px;
}
.indexpage .auto_list h3 .price {
  font-size: 16px;
  color: #FF5000;
}
.indexpage .auto_list .add_button {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}
.indexpage .auto_list .add_button .control {
  width: 100px;
  display: flex;
  justify-content: space-around;
  font-size: 18px;
  color: #999999;
}
.indexpage .auto_list .add_button .increase {
  width: 90px;
  height: 30px;
  background: #ff5722;
  border: none;
  border-radius: 5px;
  color: #ffffff;
  margin-right: 10px;
}
